import { worker } from "cluster";
import { da } from "date-fns/locale";
import { name } from "msal/lib-commonjs/packageMetadata";
import React from "react";
import { render } from "react-dom";
import '../../assets/scss/dateoff.css'
function InputTag(props) {
    return (
        <div className={props.class}>
            <label >{props.label}</label>
            <input type={props.type} className="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                placeholder={props.placeholder}
                pattern={props.pattern}
                title={props.title}
                min={props.min}
                max={props.max}
            />
        </div>
    );
}
function AreaText(props) {
    return (
        <div>
            <h4>{props.title}</h4>
            <div className="inline ct left">
                <h4>Ngày:</h4>
                <InputTag
                    type={props.type}
                    min={props.min}
                />
            </div>
            <div className="form-floating">
                <textarea className="form-control" placeholder="Leave a comment here" id="floatingTextarea"></textarea>
                <label>Nội dung</label>
            </div>
        </div>
    );
}
const date = new Date();
const month = date.getMonth() + 1;
const minDay = date.getFullYear() + "-" + month.toString() + "-" + date.getDate();
type MyState = {
    name: '',
    workUnit: '',
    identityNumber: '',
    dayOff: null,
    numDayOff: null,
    opinionOfUnitManagement: '',
    dayOpinionOfUnitManagement: null,
    opinionOfBranchManager: '',
    dayOpinionOfBranchManager: null
};
type MyProps = {};
class DateOffForms extends React.Component<MyProps, MyState> {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            workUnit: '',
            identityNumber: '',
            dayOff: null,
            numDayOff: null,
            opinionOfUnitManagement: '',
            dayOpinionOfUnitManagement: null,
            opinionOfBranchManager: '',
            dayOpinionOfBranchManager: null
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit(event) {
        alert(this.state.name);
    }
    handleChange(event) {
        this.setState({ name: event.target.value });
    }
    render() {
        return (
            <div onSubmit={this.handleSubmit} className="div-form">
                <form>
                    <div className="label">
                        <h3>Đơn Xin Nghỉ</h3>
                        <i className="fa fa-chevron-down"> </i>
                    </div>
                    <div className="inline">
                        <InputTag
                            value={this.state.name}
                            placeholder={"Nhập họ và tên"}
                            class={"form-group input"}
                            type={"text"}
                            title={"tên không ít hơn 5 kí tự"}
                            pattern={"A-Za-z]{5,}"}
                        />
                        <InputTag
                            placeholder={"Nhập đơn vị"}
                            class={"form-group input"}
                            type={"text"}
                            title={""}
                            pattern={"A-Za-z][0-9]{5-15}"} />

                    </div>
                    <div className="inline">
                        <InputTag
                            placeholder={"Nhập cmnd"}
                            class={"form-group input"}
                            type={"text"}
                            title={"CMND không ít hơn 9 số"}
                            pattern={"[0-9]{9,}"}
                        />

                    </div>
                    <div className="inline ct">
                        <h4>Ngày bắt đầu nghỉ:</h4>
                        <InputTag
                            type={"date"}
                            class={"form-group input"}
                            min={minDay}

                        />
                        <h4>Số ngày nghỉ:</h4>
                        <InputTag
                            type={"number"}
                            max={10}
                            min={0}
                        />
                    </div>
                    <hr />
                    <AreaText
                        title={"Ý kiến của Quản lý đơn vị"}
                        type={"date"}
                        min={minDay}
                    />
                    <hr />
                    <AreaText
                        title={"Ý kiến của Quản lý Chi nhánh"}
                        type={"date"}
                        min={minDay}
                    />
                    <button type="submit" className="btn btn-primary">Xác Nhận</button>
                </form>
            </div>
        );
    }
}
export default DateOffForms;